.toggler {
  display: block;
  position: relative;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

.toggler__input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none;
}

.toggler__span {
  display: block;
  width: 25px;
  height: 2px;
  margin-bottom: 5px;
  position: relative;
  background-color: $c-main;
  z-index: 1;
  transform-origin: 2px 0;
  transition: transform $half-cubic,
    background-color $half-cubic,
    opacity 0.55s ease;
}

.toggler__span:first-child {
  transform-origin: 0% 0%;
}

.toggler__span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

.toggler .toggler__input:checked ~ .toggler__span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: $c-main;
}

.toggler__input:checked ~ .toggler__span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

.toggler__input:checked ~ .toggler__span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px);
}

#console {
  width: 101vw;
  left: calc(-100vw + 140px);
  position: absolute;
  padding: 65px;
  padding-top: 30px;
  background: $c-deep-green;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  transition: transform $half-cubic;

  @media (max-width: 759px) {
    left: calc(-100vw + 120px);
    padding: 0 20px;
  }
}

.toggler__input:checked ~ ul#console {
  transform: none;
}
