.wrapper {
  margin: auto;
  max-width: 1600px;
  padding: 0 55px;
  height: 800px;

  @media (max-width: 1199px) {
    padding: 0 100px;
    height: 768px;
  }

  @media (max-width: 759px) {
    padding: 0 20px;
  }
}

.header {
  background-color: $c-deep-green;
  overflow: hidden;
  position: relative;

  @media (max-width: 759px) {
    height: 584px;
  }

  &__console {
    height: 80px;
    padding: 0 55px;

    @media (max-width: 1199px) {
      padding: 0 40px;
    }

    @media (max-width: 759px) {
      padding: 0 20px;
    }
  }

  &__hat {
    display: grid;
    column-gap: 20px;
    grid-template-columns: 160px 100px;
    grid-template-rows: 80px;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__logo {
    padding: 20px 0;
  }

  &__navigation {
    display: flex;
    align-items: center;
  }

  &__dates {
    padding-top: 100px;

    &--bottom {
      position: relative;
      margin-top: 110px;
      font-family: "Playfair Display", serif;
      font-weight: 700;
      font-size: 14px;
      line-height: 125%;
      opacity: 0.7;

      @media (max-width: 1199px) {
        display: none;
      }

      &::after {
        content: "";
        display: block;
        background-color: $c-main;
        width: 492px;
        height: 1px;
        position: absolute;
        top: 50%;
        left: 10%;
        opacity: 0.3;
      }
    }
  }

  &__title {
    margin-top: 10px;
    @include title;

    @media (max-width: 1199px) and (min-width: 760px) {
      font-size: 72px;

      &--shifted {
        padding-left: 80px;
      }
    }

    @media (max-width: 759px) {
      font-size: 48px;
    }
  }

  &__shifted {
    margin-left: 100px;

    @media (max-width: 1199px) {
      margin-left: 0;
    }

    @media (max-width: 759px) {
      margin-left: 0;
    }
  }

  &__notice {
    margin: 40px 0 60px;
    width: 270px;

    @media (max-width: 1199px) {
      width: 237px;
      margin: 40px 0 80px;
    }

    @media (max-width: 759px) {
      margin: 30px 0 50px;
    }
  }

  &__image {
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 0;

    @media (max-width: 1199px) {
      width: 389px;
    }

    @media (max-width: 759px) {
      width: 225px;
      right: -80px;
    }
  }

  &__picture-info {
    position: absolute;
    top: 54%;
    right: 55px;

    @media (max-width: 1199px) {
      display: none;
    }
  }
}
