.button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 270px;
  height: 60px;
  background-color: $c-orange;
  font-size: 16px;
  color: $c-main;
  transition: background-color 0.5s, border 0.5s;

  @media (max-width: 759px) {
    width: 280px;
  }

  &:hover {
    animation: flash 2s infinite;
    background-color: $c-deep-green;
    border: 1px solid $c-main;
  }

  @keyframes flash {
    0% {
      box-shadow: 0 0 0 0 rgba(11, 14, 14, 0.4);
    }

    70% {
      box-shadow: 0 0 0 15px rgba(11, 14, 14, 0);
    }

    100% {
      box-shadow: 0 0 0 0 rgba(11, 14, 14, 0);
    }
  }

}
