.navbar {
  height: 900px;
  position: relative;

  &__info {
    display: flex;

    @media (max-width: 1199px) {
      flex-direction: column;
    }
  }

  &__info-text {
    font-size: 14px;
    width: 220px;

    @media (max-width: 759px) {
      display: none;
    }
  }

  &__info-title {
    font-weight: 200;
  }

  &__list {
    margin: 60px 0 80px;

    @media (max-width: 759px) {
      margin: 80px 0 0;
    }

    &::after {
      content: "";
      display: block;
      background-color: #f1f5f4;
      width: 570px;
      height: 1px;
      opacity: 0.3;

      @media (max-width: 759px) {
        display: none;
      }
    }
  }

  &__link {
    display: block;
    font-family: "Playfair Display", serif;
    color: $c-main;
    font-weight: bold;
    font-size: 48px;
    line-height: 120%;
    transition: color 0.3s ease;
    padding: 0 0 60px;

    @media (max-width: 759px) {
      font-size: 24px;
    }

    &:hover {
      color: $c-orange;
    }
  }

  &__buttons {
    display: flex;

    @media (max-width: 759px) {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__button {
    margin-right: 30px;
  }

  &__image {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;

    @media (max-width: 1199px) {
      display: none;
    }
  }

  &__lang-form {
    align-self: center;
    visibility: hidden;
  }

  &__lang-switch {
    @include rale(16px, 500, 24px);
    color: #f1f5f4;
    width: 48px;
    border: 0;
    height: 34px;
    background: url(../images/drop-down.svg) no-repeat right;
    -webkit-appearance: none;
    background-position-x: 30px;

    &:focus {
      border: none;
      outline: none;
    }

    option {
      color: black;
    }
  }
}
