@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin title {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 96px;
  line-height: 100%;
  color: $c-main;
}

@mixin second-title {
  font-weight: 700;
  color: $c-second;
  font-family: "Playfair Display", serif;
  font-size: 48px;
  line-height: 120%;
}

@mixin rale($fs, $fw, $lh) {
  font-family: "Raleway", serif;
  font-size: $fs;
  font-weight: $fw;
  line-height: $lh;
}

@mixin fair($fs, $fw, $lh) {
  font-family: "Playfair Display", serif;
  font-size: $fs;
  font-weight: $fw;
  line-height: $lh;
}
